:root {
    --total-frames: 16;
}

body {
    margin: 50px;
    -webkit-user-select: none;
    -moz-user-select: none;
    -ms-use-select: none;
    user-select: none;
}

[data-product-viewer] {
    overflow: hidden;
    cursor: ew-resize;
    user-select: none;
    -webkit-user-select: none;
    -moz-user-select: none;
    -ms-user-select: none;
}

[data-product-viewer] > div {
    position: relative;
}

[data-product-viewer] img {
    width: 100%;
    display: block;
    -webkit-user-drag: none;
    -khtml-user-drag: none;
    -moz-user-drag: none;
    -o-user-drag: none;
    user-drag: none;
}

[data-product-viewer].start .product-image-preview{
    opacity: 0;
}

[data-product-viewer] [data-product-images] {
    position: absolute;
    top: 0;
    left: 0;
    width: calc(var(--view-num) * 100%);
    height: 100%;
    transform: translateX(calc(var(--current-section, 0) * var(--move-value, 0) * -1));

    display: flex;
    align-items: center;
    justify-content: flex-start;
}

[data-product-viewer] [data-product-images] img{
    flex: 1;
}

